body {color: #333; font-family: "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;}
input,
button,
textarea {font-family: "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;}

/* 공통 */
.inner {padding-left: 20px; padding-right: 20px;}
body::-webkit-scrollbar {display: none;}
body {-ms-overflow-style: none; scrollbar-width: 0px;}
.scr_x::-webkit-scrollbar {display: none;}
.scr_x {-ms-overflow-style: none; scrollbar-width: 0px;}
.img_100 img {width: 100%;}

/* 레이아웃 */
#wrap {width: 100%; min-height: 100vh;}
.wrap_inner {display: flex; max-width: 1200px; width: 100%; min-height: 100vh; margin: 0 auto;}

/* 컨텐츠 */
.common_form {max-width: 600px; width: 100%; border-left: 1px solid #000; }
.app_conent {position: relative; max-width: 600px; width: 100%; margin: 0 auto; border-left: 1px solid #333; border-right: 1px solid #333;}

/* 헤더 */
.app_conent #header {position: sticky; top: 0; z-index: 200; height: 80px; background-color: #fff; border-bottom: 1px solid #333;}
.app_conent #header > .inner {display:flex; align-items:center; width: 100%; height: 100%;}
.app_conent #header .logo {}
.app_conent #header .logo > a {font-size: 18px; font-weight: 700;}
.app_conent #header .menu {display:flex; margin-left: auto;}
.app_conent #header .menu > li {}
.app_conent #header .menu > li + li {margin-left: 20px;}
.app_conent #header .menu > li:hover > a {text-decoration: underline;}
.app_conent #header .menu > li > a.active {text-decoration: underline;}

/* 슬라이드 */
.intro_swp {position: sticky !important;}
.intro_swp1 {transform:rotate(-7deg); top:80px;}
.intro_swp2 {transform:rotate(10deg); top:140px}
.intro_swp {pointer-events : none;}
.intro_swp .swiper-wrapper {transition-timing-function:linear !important;}
.intro_swp .swiper-wrapper .title {font-size: 60px; color: #fff; -webkit-text-stroke: 1px #333; white-space: nowrap; font-weight: 800;}

/* 소개 */
#intro .intro_me {margin-top: -30px; padding-bottom: 50px; border-bottom: 1px solid #333;}
#intro .intro_me .thum {width: 280px; border: 1px solid #333; font-size: 0;}
#intro .intro_me .thum img {width: 100%;}
#intro .intro_me .cont {margin-top: 40px;}
#intro .intro_me .cont .tit {margin-bottom: 20px; font-size: 20px; font-weight: 600;}
#intro .intro_me .cont .desc {font-size: 18px; line-height: 1.8; color: #444;}
#intro .intro_me .cont .desc b {display: inline-block; position: relative; font-weight: 500; color: #111;}
#intro .intro_me .cont .desc b::before {content: ''; position: absolute; left: 0; bottom: 5px; z-index: -1; width: 100%; height: 11px; background-color: #fff3eb;}

/* 소개 리스트 */
.intro_profile {padding: 50px 0;}
.profile_list {}
.profile_list > li {}
.profile_list > li + li {margin-top: 40px;}
.profile_list > li .tit {margin-bottom: 20px; font-size: 24px; font-weight: 800;}
.profile_list > li .desc {}
.profile_list > li .desc > li {font-size: 18px; line-height: 1.4; padding-left: 10px; text-indent: -10px;}
.profile_list > li .desc > li::before {content: '-'; padding-right: 5px;}
.profile_list > li .desc > li + li {margin-top: 10px;}
.profile_list > li .desc > li span {font-size: 15px; color: #aaa;}

/* 포폴 리스트 */
.portfolio_list {}
.portfolio_list > li {border-bottom: 1px solid #333; padding: 30px 0;}
.portfolio_list > li .box {}
.portfolio_list > li .box .thum {position: relative; height: 300px; overflow: hidden; border: 1px solid #333; cursor: pointer;}
.portfolio_list > li .box .thum img { position: absolute; top: 0; left: 0; width: 100%; transition: transform 0.5s linear;}
.portfolio_list > li .box .cont {position: relative; margin-top: 20px;}
.portfolio_list > li .box .cont > li {font-size: 16px; display:flex; line-height: 1.6; word-break:keep-all ;}
.portfolio_list > li .box .cont > li.name {font-size: 20px; font-weight: 800; margin-bottom: 20px; }
.portfolio_list > li .box .cont > li + li {margin-top: 10px;}
.portfolio_list > li .box .cont > li > b {white-space: nowrap; font-weight: 700;}
.portfolio_list > li .box .cont > li > b::after {content: ':'; margin: 0 7px;}
.portfolio_list > li .box .cont .link {position: absolute; top: 0; right: 0; margin: 0;}
.portfolio_list > li .box .cont .link > a {font-size: 15px; font-weight: 600;}
.portfolio_list > li .box .cont .link > a:hover {text-decoration: underline;}
.portfolio_list > li .box .cont .link > a i {margin-left: 5px;}

/* 포폴 팝업 */
#portfolioview {height: calc(100dvh - 80px); overflow: auto; position: sticky; top: 200px; margin-bottom: -120px; transform: translateY(-120px);}
#portfolioview .portfolioview_box {padding-top: 120px;}
.pf_view_box {display:flex; align-items:center; justify-content:center; flex-direction:column; height: calc(100dvh - 200px); padding-bottom: 200px; overflow: auto; position: sticky; top: 200px; font-size: 18px; font-weight: 600;}
.pf_view_box i {margin-bottom: 20px; font-size: 24px;}

/* 메시지 form */
#form_box {position: sticky; top: 0; width: 100%;}
#form_box > .inner {width: 100%;}
#form_box > .inner > form {padding: 20px 0;}
#form_box .form_top {margin-top: 30px; margin-bottom: 40px;}
#form_box .form_top .tit {font-size: 40px; font-weight: 800; margin-bottom: 30px;}
#form_box .form_top .desc {font-size: 18px; line-height: 1.8;}
#form_box .form_list {border-top: 1px solid #333;}
#form_box .form_list > li .tit {display: none;}
#form_box .form_list > li .cont {}
#form_box .form_list > li .cont input {display:block; overflow:visible; padding:0; border:none; box-shadow:none; border-radius:0; background: inherit; outline: none;}
#form_box .form_list > li .cont input {width: 100%; height: 60px; border-bottom: 1px solid #333; padding: 0 20px; font-size: 16px; }
#form_box .form_list > li .cont textarea {display:block; overflow:visible; padding:0; border:none; box-shadow:none; border-radius:0; background: inherit; resize: none; outline: none;}
#form_box .form_list > li .cont textarea {width: 100%; height: 250px; border-bottom: 1px solid #333; padding: 20px; font-size: 16px;}
#form_box .form_btn button {display:block; overflow:visible; padding:0; border:none; box-shadow:none; border-radius:0; background: inherit; cursor:pointer; outline: none;}
#form_box .form_btn button {width: 100%; height: 60px; display:flex; align-items:center; justify-content:center; border-bottom: 1px solid #333; font-size: 17px; font-weight: 600; transition:all 0.2s;}
#form_box .form_btn button:hover {background-color: #f2f2f2;}

/* 연락처 */
#phone {display: flex; align-items:flex-end; width: 100%; height: 100dvh; position: sticky; top: 0;}
#phone .phone_box {width: 100%;}
#phone .phone_list {width: 100%;}
#phone .phone_list > li {position: relative; border-top: 1px solid #333;}
#phone .phone_list > li > a {display:flex; align-items:center; height: 60px; padding: 0 20px; font-size: 18px; transition:all 0.2s;}
#phone .phone_list > li > a > b {display:flex; align-items:center; width: 120px; height: 60px; margin-right: 20px; border-right: 1px solid #333;}
#phone .phone_list > li > a:hover {background-color: #f2f2f2;}
#phone .phone_list > li > .thum_pop {position: absolute; bottom: calc(100% + 10px); left: 10px; width: 300px; height: 370px; border: 1px solid #333;}
#phone .phone_list > li > .thum_pop img {width: 100%; height: 100%; object-fit:cover;}

.introme_swp {overflow: hidden;}
.introme_swp .img_100 {height: 200px; width: 100%; overflow: hidden;}
/* 반응형 */
@media all and (max-width:1200px) {
    .portfolio_list > li:last-child {border-bottom: 0;}
    #form_box {position: static;}
    #phone {height: auto; position: static; padding-top: 100px;}
}

@media all and (max-width:600px) {
    #wrap {padding: 0 15px;}
    #phone {padding-top: 50px;}
    .inner {padding-left: 10px; padding-right: 10px;}


    /* 헤더 */
    .app_conent #header {height: 60px;}
    .app_conent #header .logo > a {font-size: 16px;}
    .app_conent #header .menu > li + li {margin-left: 15px;}
    .app_conent #header .menu > li > a {font-size: 13px;}

    /* 슬라이드 */
    .intro_swp1 {top:70px;}
    .intro_swp2 {top:100px}
    .intro_swp .swiper-wrapper .title {font-size: 30px; }

    /* 소개 */
    #intro .intro_me {padding-bottom: 30px;}
    #intro .intro_me .thum {width: 230px;}
    #intro .intro_me .cont {margin-top: 30px;}
    #intro .intro_me .cont .tit {margin-bottom: 15px; font-size: 16px;}
    #intro .intro_me .cont .desc {font-size: 13px; line-height: 1.6;}
    #intro .intro_me .cont .desc b::before {height: 7px;}

    /* 소개 리스트 */
    .intro_profile {padding: 30px 0;}
    .profile_list {}
    .profile_list > li {}
    .profile_list > li + li {margin-top: 30px;}
    .profile_list > li .tit {margin-bottom: 20px; font-size: 16px;}
    .profile_list > li .desc {}
    .profile_list > li .desc > li {font-size: 14px; }
    .profile_list > li .desc > li span {font-size: 12px;}

    /* 포폴 리스트 */
    .portfolio_list {}
    .portfolio_list > li {border-bottom: 1px solid #333; padding: 30px 0;}
    .portfolio_list > li .box {}
    .portfolio_list > li .box .thum {height: 230px; overflow: auto;}
    .portfolio_list > li .box .thum img { position: static !important; transform: translateY(0px) !important; pointer-events: none;}
    .portfolio_list > li .box .cont {margin-top: 10px;}
    .portfolio_list > li .box .cont > li {font-size: 13px;}
    .portfolio_list > li .box .cont > li.name {font-size: 16px;}
    .portfolio_list > li .box .cont > li > b::after {margin: 0 7px;}
    .portfolio_list > li .box .cont .link {top: 5px;}
    .portfolio_list > li .box .cont .link > a {font-size: 12px;}
    .portfolio_list > li .box .cont .link > a i {margin-left: 3px;}

    /* 포폴 팝업 */
    #portfolioview {display: none !important;}

    /* 메시지 form */
    #form_box .form_top {margin-top: 20px; margin-bottom: 20px;}
    #form_box .form_top .tit {font-size: 26px; margin-bottom: 20px;}
    #form_box .form_top .desc {font-size: 13px; word-break: keep-all;}
    #form_box .form_list > li .cont input {height: 45px; padding: 0 10px; font-size: 13px; }
    #form_box .form_list > li .cont textarea {height: 200px; padding: 10px; font-size: 13px;}
    #form_box .form_btn button {height: 45px; font-size: 13px;}

    /* 연락처 */
    #phone .phone_list > li > a {height: 35px; padding: 0 10px; font-size: 13px;}
    #phone .phone_list > li > a > b {width: 70px; height: 35px; margin-right: 10px;}
    #phone .phone_list > li > .thum_pop {width: 150px; height: 185px;}
}